body {
  justify-content: center;
  margin-top: 30px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.people {
  grid-template-columns: repeat(2, 200px);
  gap: 30px 40px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  text-align: center;
  border-radius: 10px;
  width: 220px;
  height: 287px;
  transition: transform 1s;
}

.person__photo {
  position: relative;
}

.person__image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person__image:hover {
  border-radius: 25%;
}

.person__details {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person:hover .person__extra {
  opacity: 0;
  pointer-events: none;
  transition: opacity 1s;
}

.person__extra:hover {
  opacity: 1;
}
